import React, { useState, useEffect } from 'react'
import {
  Button,
  StylesProvider,
  Container,
  Grid,
  Card,
  CardMedia,
  CardContent,
} from '@material-ui/core'
import { useParams, useLocation } from 'react-router-dom'
import CircularStatic from '../commons/CircularProgressWithLabel'
import './Profile.css'
import chef from '../images/chef_.jpg'
import DonateNFT from '../donate-nft/DonateNFT'

function Profile({ account }) {
  const { recipeId } = useParams()
  const [loading, setLoading] = useState(false)
  const userWallet = '0xAF67cbD8fb00759C3b4667beAcfBB3600e25476A'
  // const userWallet = '0x5Df598c222C4A7e8e4AB9f347dcBd924B6458382'
  const [nfts, setNfts] = useState([])
  const [data, setData] = useState({})
  const [donating, setDonating] = useState(false)
  const { state = {} } = useLocation()

  const covalentNfts = async () => {
    const covalentAPI = 'ckey_748a7d4669e44381acb018c5d6a'
    try {
      const historyResult = await fetch(
        `https://api.covalenthq.com/v1/137/address/${userWallet}/balances_v2/?nft=true&key=${covalentAPI}`,
      )
      const { data } = await historyResult.json()
      console.log(' data covalentAPI', data)

      if (data) {
        setNfts(data.items[0].nft_data)
        setLoading(false)
      }
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    setLoading(true)
    covalentNfts()
  }, [])

  useEffect(() => {
    const getImage = (ipfsURL) => {
      if (!ipfsURL) return
      ipfsURL = ipfsURL.split('://')
      return 'https://ipfs.io/ipfs/' + ipfsURL[1]
    }

    const getMetadata = async () => {
      let data = await fetch(`https://ipfs.io/ipfs/${recipeId}/metadata.json`)
      data = await data.json()
      const dataArray = data.description.split(',')
      data.creator = dataArray[0]
      data.type = dataArray[1]
      data.intro = dataArray[2]
      data.image = getImage(data.image)
      setData(data)
    }
    if (recipeId) {
      getMetadata()
      getImage()
    }
  }, [recipeId, account])

  return (
    <StylesProvider injectFirst>
      <Container style={{ paddingTop: '1rem' }}>
        <img src={chef} alt="profile" className="profile-img" />
        <h2>{state?.recipe?.creator}</h2>
        <Button variant="contained" className="connect-wallet-btn">
          + Follow
        </Button>{' '}
        &nbsp;
        <Button
          variant="contained"
          className="connect-wallet-btn"
          onClick={() => setDonating(true)}
        >
          Donate to creator
        </Button>
        {donating ? <DonateNFT /> : ''}
        <h3>NFTs from Covalent API</h3>
        {loading ? (
          <CircularStatic />
        ) : (
          <div>
            {nfts && nfts.length ? (
              nfts.map((project, index) => (
                <Card className="card-padding" key={index}>
                  <Grid container spacing={1}>
                    <Grid item xs={4} md={2}>
                      <img
                        className="nft-img"
                        src={project.external_data.image}
                        alt="nft"
                      />
                    </Grid>
                    <Grid item xs={8} md={10} className="nft-col2">
                      <div className="container-flex">
                        <h2 className="inner2">{project.external_data.name}</h2>
                        <p className="info">
                          TokenId: <strong>{project.token_id}</strong>
                        </p>
                      </div>

                      <p className="info">
                        <strong>Token Balance: </strong>
                        {project.token_balance}
                      </p>
                      <p className="info">
                        <strong>Tokens supported: </strong>
                        {project.supports_erc.length > 0 ? (
                          project.supports_erc.map((index) => (
                            <span>{index}, </span>
                          ))
                        ) : (
                          <p>ERC20</p>
                        )}
                      </p>
                      <p className="info">
                        <strong>Owner: </strong>
                        {project.owner}
                      </p>
                      <p className="info">
                        <strong>Original owner: </strong>
                        {project.original_owner}
                      </p>
                      <p className="info">
                        <strong>Desc: </strong>
                        {project.external_data.description}
                      </p>
                    </Grid>
                  </Grid>
                </Card>
              ))
            ) : (
              <h2>No NFTs Yet...</h2>
            )}
          </div>
        )}
      </Container>
    </StylesProvider>
  )
}

export default Profile
