import React from 'react'
import './App.css'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { Navbar } from './components/layout/navbar/Navbar'
import Footer from './components/layout/footer/Footer'
import Home from './components/home/Home'
import DetailsRecipie from './components/details-recipe/DetailsRecipe'
import CreateRecipe from './components/create-recipe/CreateRecipe'
import LadingPage from './components/landing-page/LandingPage'
import Profile from './components/profiles/Profile'
import CovalentAllNftsByContract from './components/covalent-allNftsByContract/CovalentAllNftsByContract'
import Web3 from 'web3'
import { useMoralis } from 'react-moralis'
import playerAbi from './artifacts/contracts/Player.sol/Player.json'
const chefContractAddress = '0x1a2FCb5F2704f1fF8eFF26668f63D001b42bF80B'

function App() {
  const [account, setAccount] = React.useState('')
  const [contractData, setContractData] = React.useState('')
  const { authenticate, isAuthenticated, user } = useMoralis()

  const moralisLogin = async () => {
    if (!isAuthenticated) {
      return (
        <div>
          <button onClick={() => authenticate()}>Authenticate</button>
        </div>
      )
    }
  }

  const loadWeb3 = async () => {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum)
      await window.ethereum.request({ method: 'eth_requestAccounts' })
    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
    } else {
      window.alert(
        'Non-Ethereum browser detected. You should consider trying Metamask!',
      )
    }
  }

  const getContract = async () => {
    try {
      const web3 = window.web3
      const accounts = await web3.eth.getAccounts()
      setAccount(accounts[0])
      const abi = playerAbi.abi
      const address = chefContractAddress
      const myContract = new web3.eth.Contract(abi, address)
      setContractData(myContract)
    } catch (error) {
      window.alert(
        'Contract is not deployed to the detected network. Please connect to Mumbai network!',
      )
    }
  }

  const connectWallet = async () => {
    console.log('login')
    await loadWeb3()
    await getContract()
  }

  console.log('contractData', contractData)

  return (
    <Router className="App">
      <div className="App">
        <Navbar
          authenticate={authenticate}
          isAuthenticated={isAuthenticated}
          connectWallet={connectWallet}
          user={user}
          account={account}
        />
        <Route exact path="/" component={LadingPage} />
        <Route exact path="/collections" component={Home} />

        <Switch>
          <Route exact path="/create-recipe" component={CreateRecipe} />
          <Router path="/profile/:profileId">
            <Profile user={user} account={account} />
          </Router>

          <Route path="/recipe/:recipeId">
            <DetailsRecipie account={account} contractData={contractData} />
          </Route>

          <Route path="/all-nfts-contract">
            <CovalentAllNftsByContract
              account={account}
              contractAddress={chefContractAddress}
            />
          </Route>
        </Switch>
        <Footer />
      </div>
    </Router>
  )
}

export default App
